.transport__title {
  font-weight: 700;
  text-transform: uppercase;

  //Tablet
  @media (min-width: 768px){
    font-size: 20px;
  }

  &--form {
    //Tablet
    @media (min-width: 768px){
      margin-left: 20px;
    }
  }
}

.transport__list {
  display: flex;
  justify-content: center;
  list-style: none;
  margin: 0;
  padding: 0;

  &--filter {
    justify-content: flex-start;
    padding: 13px 0 31px;

    //Tablet
    @media (min-width: 768px) and (max-width: 1439px) {
      padding: 0;
    }
  }

  &--catalog {
    justify-content: flex-start;
  }
}

.transport__item {
  margin-left: 33px;

  &--filter {
    //Tablet
    @media (min-width: 768px) and (max-width: 1439px) {
      margin-left: 25px;
      margin-right: 19px;
    }
  }

  &--form {
    //Tablet
    @media (min-width: 768px) and (max-width: 1439px) {
      margin-left: 44px;
    }
  }
}

.transport__item label {
  position: relative;
  display: block;
}

.transport__item-checkbox {

