  content: "";
  display: block;
  position: absolute;
  box-sizing: border-box;
  left: -33px;
  top: -1px;
  height: 31px;
  width: 31px;
  background-color: #ffffff;
  border-radius: 25px;

  //Tablet
  @media (min-width: 768px) and (max-width: 1439px) {
